import React, { useEffect, useRef, useState } from 'react';
import { useParams } from 'react-router-dom';

import gsap from 'gsap';
import {Back, TextPlugin} from 'gsap/all';

import config from 'globals/config';

import useDeviceMotion from 'components/useDeviceMotion';
import withSocketio from 'components/withSocketio';

import './main.css';
gsap.registerPlugin(TextPlugin);

const scenes = {
  // 0:  'Start',
  0:  'Idle',
  1:  'WaitForPlayer',
  2:  'ModePick',
  3:  'WaitForPlayerMode',
  4:  'ModeResult',
  5:  'GameOneReady',
  6:  'GameOnePlay',
  7:  'GameoneResult',
  8:  'GameTwoReady',
  9:  'GameTwoPlay',
  10: 'GameTwoResult',
}
const gameTypeArray = [
  'Day',
  'Night'
];
const Main = () => {
  const params = useParams();
  const playerId = params['playerId'];
  const [{moveCounter, permissionGranted},{requestPermission, setMoveCounter}] = useDeviceMotion();
  const [errorMsg, setErrorMsg] = useState("");
  const [game1Result, setGame1Result] = useState("");

  console.log('playerId: ', playerId);
  const globalTimelineRef = useRef(null);
  const socket = useRef(null);

  const selectMode = (mode) => {
    if (socket.current) {
      socket.current.emit('selectGame', {
        data: mode
      })
    }
  }

  const toScene0 = () => {
    if (globalTimelineRef.current)
      globalTimelineRef.current.clear();
    globalTimelineRef.current = gsap.timeline({
      repeat: -1,
      repeatDelay: 3
      // yoyo: true
    })
    gsap.set('#mainCircle', {
      scale: 1,
      autoAlpha: 1,
      transformOrigin: "center"
    })
    gsap.set('.svgCircle', {
      scaleX: 2,
      scaleY: 1,
      rx: 0,
      transformOrigin: "center"
    })
    gsap.set('#circle1', {
      autoAlpha: 0,
      r: 4
    })
    gsap.set('.scene', {
      autoAlpha: 0
    })
    gsap.set('.scene.scene0', {
      autoAlpha: 1
    })
    gsap.set('#circle1', {
      r: 4
    })
    gsap.set('.scene7 .centerText', {
      color: '#00000000'
    })
    gsap.set('#circleBackground', {
      r: 0,
      fill: 'none'
    })
    
    globalTimelineRef.current.add(
      gsap.to('.scene0 .joinGameButton', {
        rotate: -5,
        duration: 0.1,
        ease: "elastic.out(1, 0.75)"
      })
    )
    globalTimelineRef.current.add(
      gsap.to('.scene0 .joinGameButton', {
        rotate: 0,
        duration: 1,
        ease: "elastic.out(2, 0.2)"
      })
    )
  }
  const toScene1 = () => {
    if (globalTimelineRef.current)
      globalTimelineRef.current.clear();
    globalTimelineRef.current = gsap.timeline({
      repeat: -1,
      paused: true,
      // yoyo: true
    });
    const svgCircles = Array.from(document.querySelectorAll('.svgCircle')).reverse();
    const tl = gsap.timeline();
    tl.add([
      gsap.to(svgCircles, {
        scaleX: 1,
        scaleY: 1,
        rx: 100,
        autoRound: false,
        duration: 0.5
      }),
      gsap.to('.scene.scene0', {
        autoAlpha: 0
      })
    ]);
    tl.add([
      gsap.set('#circle1', {
        autoAlpha: 0
      }),
      gsap.set('.scene.scene1', {
        autoAlpha: 1
      }),
    ]);
    tl.add(() => {globalTimelineRef.current.play()});
    globalTimelineRef.current.add([
      gsap.timeline().staggerFromTo(svgCircles, 0.6, {
        scale: 1,
        autoRound: false,
      }, {
        scale: 1.1,
        autoRound: false,
        ease: "power1.out"
      }, 0.3),
      gsap.timeline().staggerFromTo(svgCircles, 0.6, {
        scale: 1.1,
        autoRound: false,
      }, {
        scale: 1,
        autoRound: false,
        delay: 0.6,
        ease: "power1.in"
      }, 0.3)
    ])
  }
  const toScene2 = () => {
    if (globalTimelineRef.current)
      globalTimelineRef.current.clear();
    const tl = gsap.timeline();
    tl.add([
      gsap.set('.scene2 .option', {
        scale: 0,
        color: 'transparent'
      })
    ])
    tl.add(
      gsap.to('#mainCircle', {
        scale: 4,
        transformOrigin: "center",
        duration: 0.5
      })
    )
    tl.add(
      gsap.to('.scene1', {
        autoAlpha: 0
      })
    )
    tl.add(
      gsap.to('.scene2', {
        autoAlpha: 1
      })
    )
    tl.add([
      gsap.to('.scene2 .option1', {
        scale: 1
      }),
      gsap.to('.scene2 .option2', {
        scale: 1
      })
    ])
    tl.add([
      gsap.to('.scene2 .option1', {
        color: '#000000'
      }),
      gsap.to('.scene2 .option2', {
        color: '#FFFFFF'
      })
    ])
  }
  const toScene3 = () => {
    if (globalTimelineRef.current)
      globalTimelineRef.current.clear();
    globalTimelineRef.current = gsap.timeline({
      repeat: -1,
      paused: true
    });
    const tl = gsap.timeline();
    gsap.set('#circle1', {
      r: 4,
      autoAlpha: 0
    })
    gsap.set('.svgCircle', {
      autoAlpha: 0
    })
    gsap.set('#mainCircle', {
      scale: 1
    })
    tl.add(
      gsap.to('.scene2', {
        autoAlpha: 0
      })
    )
    tl.add([
      gsap.to('.scene3', {
        autoAlpha: 1
      })
    ])
    const svgCircles = Array.from(document.querySelectorAll('.svgCircle')).reverse();
    tl.add(() => {globalTimelineRef.current.play()});
    tl.add([
      gsap.timeline().staggerTo(svgCircles, 0.6, {
        autoAlpha: 1
      }, 0.3),
      // /
    ])
    globalTimelineRef.current.add([
      gsap.timeline().staggerFromTo(svgCircles, 0.6, {
        scale: 1,
        autoRound: false,
      }, {
        scale: 1.1,
        autoRound: false,
        ease: "power1.out"
      }, 0.3),
      gsap.timeline().staggerFromTo(svgCircles, 0.6, {
        scale: 1.1,
        autoRound: false,
      }, {
        scale: 1,
        autoRound: false,
        delay: 0.6,
        ease: "power1.in"
      }, 0.3)
    ])
  }
  const toScene4 = () => {
    if (globalTimelineRef.current)
      globalTimelineRef.current.clear();
    
    const tl = gsap.timeline();
    gsap.set('#circle1', {
      r: 4,
      autoAlpha: 0
    })
    gsap.set('#circle1', {
      r: 3.5,
      // r: game1Result === "Day"? 3.5: 4.2,
      autoAlpha: 0
    })
    gsap.set('.scene4 .resultText', {
      autoAlpha: 0
    })
    gsap.to('#mainCircle', {
      scale: 2
    })
    tl.add([
      gsap.to('.scene2', {
        autoAlpha: 0
      }),
      gsap.to('.scene3', {
        autoAlpha: 0
      })
    ])
    tl.add([
      gsap.fromTo('.scene4', {
        autoAlpha: 0,
        color: 'transparent'
      }, {
        autoAlpha: 1,
        color: '#000000'
        // color: game1Result === "Day"? '#000000': '#FFFFFF'
      }),
      gsap.fromTo('#circleBackground', {
        r: 0,
        fill: '#FFFFFF'
        // fill: game1Result === "Day"? '#FFFFFF': '#000000'
      }, {
        autoRound: false,
        r: 2.5
      })
    ])
    tl.add([
      gsap.to('.scene4 .resultText', {
        autoAlpha: 1,
        delay: 2.6
      })
    ])
    
  }
  const toScene5 = () => {
    if (globalTimelineRef.current)
      globalTimelineRef.current.clear();
    const tl = gsap.timeline();
    tl.add(
      gsap.to('.scene4', {
        autoAlpha: 0,
        duration: 0.5
      })
    )
    gsap.set('.scene5 .centerText', {
      text: {
        value: 3
      },
      color: '#000000'
      // color: game1Result === "Day"? '#000000': '#FFFFFF'
    })
    tl.add(
      gsap.to('.scene5', {
        autoAlpha: 1,
        duration: 0.5
      })
    )
    // tl.add(
    //   gsap.fromTo('.scene5 .centerText', {
    //     text: {
    //       value: 3
    //     },
    //     y: '-50%',
    //     scale: 0,
    //     autoAlpha: 0
    //   }, {
    //     y: '-50%',
    //     scale: 1,
    //     autoAlpha: 1,
    //     duration: 1
    //   })
    // )
    tl.add(
      gsap.fromTo('.scene5 .centerText', {
        text: {
          value: 2
        },
        // y: '-50%',
        scale: 0,
        autoAlpha: 0
      }, {
        // y: '-50%',
        scale: 1,
        autoAlpha: 1,
        duration: 1
      })
    )
    tl.add(
      gsap.fromTo('.scene5 .centerText', {
        text: {
          value: 1
        },
        // y: '-50%',
        scale: 0,
        autoAlpha: 0
      }, {
        // y: '-50%',
        scale: 1,
        autoAlpha: 1,
        duration: 1
      })
    )
  }
  const toScene6 = () => {
    const tl = gsap.timeline();
    gsap.set('.scene6 .centerText', {
      color: '#000000'
      // color: game1Result === "Day"? '#000000': '#FFFFFF'
    })
    tl.add(
      gsap.to('.scene5', {
        autoAlpha: 0,
        duration: 0.4
      })
    )
    tl.add(
      gsap.to('.scene6', {
        autoAlpha: 1,
        duration: 0.4
      })
    )
  }
  const toScene7 = () => {
    const tl = gsap.timeline();
    tl.add(
      gsap.to('.scene6', {
        autoAlpha: 0,
        duration: 0.4
      })
    )
    tl.add(
      gsap.to('.scene7', {
        autoAlpha: 1,
        duration: 0.4
      })
    )
    gsap.set('.scene7 .centerText', {
      color: '#000000'
      // color: game1Result === "Day"? '#000000': '#FFFFFF'
    })
    // tl.add(
    //   gsap.fromTo('.scene7 .centerText', {
    //     text: {
    //       value: 3
    //     },
    //     y: '-50%',
    //     scale: 0,
    //     autoAlpha: 0
    //   }, {
    //     y: '-50%',
    //     scale: 1,
    //     autoAlpha: 1,
    //     duration: 1
    //   })
    // )
    // tl.add(
    //   gsap.fromTo('.scene7 .centerText', {
    //     text: {
    //       value: 2
    //     },
    //     y: '-50%',
    //     scale: 0,
    //     autoAlpha: 0
    //   }, {
    //     y: '-50%',
    //     scale: 1,
    //     autoAlpha: 1,
    //     duration: 1
    //   })
    // )
    // tl.add(
    //   gsap.fromTo('.scene7 .centerText', {
    //     text: {
    //       value: 1
    //     },
    //     y: '-50%',
    //     scale: 0,
    //     autoAlpha: 0
    //   }, {
    //     y: '-50%',
    //     scale: 1,
    //     autoAlpha: 1,
    //     duration: 1
    //   })
    // )
  }
  const toScene8 = () => {
    const tl = gsap.timeline();
    gsap.set('.scene8 .centerText', {
      color: '#000000'
      // color: game1Result === "Day"? '#000000': '#FFFFFF'
    })
    tl.add(
      gsap.to('.scene7', {
        autoAlpha: 0,
        duration: 0.4
      })
    )
    tl.add(
      gsap.to('.scene8', {
        autoAlpha: 1,
        duration: 0.4
      })
    )
  }
  const toScene9 = () => {
    const tl = gsap.timeline();
    tl.add([
      gsap.to('#mainCircle', {
        scale: 4,
        autoAlpha: 0
      }),
      gsap.to('.scene8', {
        autoAlpha: 0,
        duration: 0.4
      })
    ])
    tl.add(
      gsap.to('.scene9', {
        autoAlpha: 1,
        duration: 0.4
      })
    )
    if (socket.current) {
      socket.current.disconnect();
    }
  }
  const toSceneError = () => {
    const tl = gsap.timeline();
    tl.add(
      gsap.to('.scene', {
        autoAlpha: 0,
      })
    )
    tl.add(
      gsap.to('.sceneError', {
        autoAlpha: 1,
      })
    )
    if (socket.current) {
      socket.current.disconnect();
    }
  }
  const doShake = () => {
    if (socket.current)
      socket.current.emit('shake');
    const tl = gsap.timeline();
    gsap.set('.shakeEffect g', {
      stroke: '#000000'
      // stroke: game1Result === "Day"? '#000000': '#FFFFFF'
    })
    tl.add(
      gsap.to('.mobileIcon', {
        rotate: 10,
        ease: Back.easeOut,
        duration: 0.1
      })
    )
    tl.add([
      gsap.to('.shakeEffect', {
        autoAlpha: 1,
        duration: 0.25
      }),
      gsap.to('.mobileIcon', {
        rotate: -15,
        duration: 0.25
      })
    ])
    tl.add([
      gsap.to('.shakeEffect', {
        autoAlpha: 0,
        duration: 0.15
      }),
      gsap.to('.mobileIcon', {
        rotate: 0,
        ease: Back.easeOut,
        duration: 0.15
      })
    ])
  }
  useEffect(() => {
    toScene0();
    // disable double tap zoom
    document.addEventListener("click", event => {});
  }, []);
  useEffect(() => {
    if (permissionGranted) {
      // socket.current = io('ws://10.0.1.40:3005');
      socket.current = withSocketio({
        host: config.serverUrl,
        eventEmitters: [
          {
            emitter: 'joinRoom',
            data: {
              playerId: playerId || 'empty'
            },
            ack: (result) => {
              console.log('joinRoomAck', result);
              if (result['data'] !== "joined") {
                setErrorMsg(result['data']);
                toSceneError();
              }
              //  else {
              //   // wait gameStage event do stage change
              //   toScene1();
              // }
              //   setGameStage(gameStatus.offline);
              //   setAlertText(result['data']);
              // } else {
              //   setPlayerColorCode(
              //     playerColorCodeOfIdx[result['index']]
              //   );
              // }
            }
          }
        ],
        eventListeners: [
          {
            listener: 'gameStage',
            callback: (data) => {
              console.log('gameStage', data);
              const stageId = data['data'];
              switch (stageId) {
                case 0:
                  toScene0();
                  break;
                case 1:
                  toScene1();
                  break;
                case 2:
                  toScene2();
                  break;
                case 3:
                  toScene3();
                  break;
                case 4:
                  toScene4();
                  break;
                case 5:
                  toScene5();
                  break;
                case 6:
                  toScene6();
                  break;
                case 7:
                  toScene7();
                  break;
                case 8:
                  // toScene8();
                  break;
                case 9:
                  toScene8();
                  break;
                case 10:
                  toScene9();
                  //   toScene10();
                  break;
              }
              // setGameStage(stageId);
              // if (stageId === gameStatus.selecting) {
              //   setGameSelected(0);
              // } else 
              // if (stageId === gameStatus.result) {
              //   socket.current.disconnect();
              // }
            }
          },
          {
            listener: 'gameResult',
            callback: (data) => {
              console.log('gameResult', data);
              // const score = data['data'];
              // setScore(Math.round(score));
            }
          },
          {
            listener: 'playersInfo',
            callback: (data) => {
              console.log('playersInfo', data);
              // const players = data['data'];
              // players.forEach(player => {
              //   if (player['playerId'] === playerId['playerId']) {
              //     setIsJoined(player['joined']);
              //   }
              // });
            }
          },
          {
            listener: 'OnPlayerSelectMode',
            callback: (data) => {
              console.log('OnPlayerSelectMode', data);
              const gameIdx = data['data'];
              setGame1Result(gameTypeArray[gameIdx]);
            }
          },
          {
            listener: 'gameSelected',
            callback: (data) => {
              console.log('gameSelected', data);
              // const gameId = data['data'];
              // setGameSelected(gameId);
              // gameSelected.current = gameId;
            }
          },
        ]
      })
      toScene1();
    }
  }, [permissionGranted])
  useEffect(() => {
    doShake();
  }, [moveCounter])
  return <div id="gameMain">
    <div className="background">
      <svg viewBox="0 0 10 10">
        <g id="mainCircle">
          {new Array(11).fill(0).map((_, idx) => {
            return <rect id={`circle${idx+2}`} className="svgCircle" rx="100" key={idx} x={5 - (27 - idx * 2) / 2} y={5 - (27 - idx * 2) / 2} width={27 - idx * 2} height={27 - idx * 2} fill={idx % 2? '#000000': '#FFFFFF'} />
          })}
          <circle id="circle1" cx="5" cy="5" r="4"
            fill="none"
            stroke="#000000"
            strokeWidth="1">
          </circle>
          <circle id="circleBackground" cx="5" cy="5" r="0" fill="none" />
        </g>
      </svg>
    </div>
    {/* <div className="reset" style={{position:'absolute',bottom:0,right:0,background:'#FFF',zIndex:99}} onPointerDown={toScene0}>reset</div> */}
    {/* <div className="debug" style={{position:'absolute',top:0,left:0,background:'#FFF',zIndex:99}} onPointerDown={()=>setMoveCounter(0)}>{moveCounter}</div> */}
    <div className="scene scene0">
      <div className="joinGameButton" onClick={requestPermission}>Join Game</div>
    </div>
    <div className="scene scene1" /*onPointerDown={toScene2}*/>
      <div className="centerText">Wait for other players ...</div>
    </div>
    <div className="scene scene2">
      <div className="centerText">Which look would you like to do?</div>
      <div className="option option1" onPointerDown={() => {
        // setGame1Result("Day");
        selectMode(0);
        toScene3();
      }}>Day</div>
      <div className="option option2" onPointerDown={() => {
        // setGame1Result("Night");
        selectMode(1);
        toScene3();
      }}>Night</div>
    </div>
    <div className="scene scene3" /*onPointerDown={toScene4}*/>
      <div className="centerText">Loading...</div>
    </div>
    <div className="scene scene4" /*onPointerDown={toScene5}*/>
      <div className="centerText">
        <div className="resultTitle">The look we are doing is...</div>
        <div className="resultText">{game1Result}!</div>
      </div>
    </div>
    <div className="scene scene5" /*onPointerDown={toScene6}*/>
      <div className="centerText"></div>
    </div>
    <div className="scene scene6">
      <div className="centerText">
        <svg viewBox="0 0 35 23" className="shakeEffect shakeEffect1">
          <g stroke="#FFF" fill="none" strokeWidth="3">
            <path d="M153.28 463.88l1.277-.34c7.393-1.973 14.995 2.385 17.029 9.762h0" transform="translate(-141 -459) rotate(-177 162.433 466.64)"/>
            <path d="M142.736 467.692l2.32-.619c13.321-3.55 27.013 4.3 30.68 17.589h0" transform="translate(-141 -459) rotate(-177 159.236 472.662)"/>
          </g>
        </svg>
        <svg viewBox="0 0 35 23" className="shakeEffect shakeEffect2">
          <g stroke="#FFF" fill="none" strokeWidth="3">
            <path d="M153.28 463.88l1.277-.34c7.393-1.973 14.995 2.385 17.029 9.762h0" transform="translate(-141 -459) rotate(-177 162.433 466.64)"/>
            <path d="M142.736 467.692l2.32-.619c13.321-3.55 27.013 4.3 30.68 17.589h0" transform="translate(-141 -459) rotate(-177 159.236 472.662)"/>
          </g>
        </svg>
        <svg className="mobileIcon" viewBox="0 0 90 157" /*onPointerDown={doShake}*/>
          <rect width="90" height="157" fill="#FFF" rx="2" stroke="#000"/>
          <rect width="81" height="121" x="5" y="14" fill="#000" rx="2"/>
          <circle cx="45" cy="146" r="6.5" stroke="#000"/>
        </svg>
        <div /*onPointerDown={toScene7}*/>Shake!</div>
      </div>
    </div>
    <div className="scene scene7" /*onPointerDown={toScene8}*/>
      <div className="centerText">Waiting...</div>
    </div>
    <div className="scene scene8">
      <div className="centerText">
        <svg viewBox="0 0 35 23" className="shakeEffect shakeEffect1">
          <g stroke="#FFF" fill="none" strokeWidth="3">
            <path d="M153.28 463.88l1.277-.34c7.393-1.973 14.995 2.385 17.029 9.762h0" transform="translate(-141 -459) rotate(-177 162.433 466.64)"/>
            <path d="M142.736 467.692l2.32-.619c13.321-3.55 27.013 4.3 30.68 17.589h0" transform="translate(-141 -459) rotate(-177 159.236 472.662)"/>
          </g>
        </svg>
        <svg viewBox="0 0 35 23" className="shakeEffect shakeEffect2">
          <g stroke="#FFF" fill="none" strokeWidth="3">
            <path d="M153.28 463.88l1.277-.34c7.393-1.973 14.995 2.385 17.029 9.762h0" transform="translate(-141 -459) rotate(-177 162.433 466.64)"/>
            <path d="M142.736 467.692l2.32-.619c13.321-3.55 27.013 4.3 30.68 17.589h0" transform="translate(-141 -459) rotate(-177 159.236 472.662)"/>
          </g>
        </svg>
        <svg className="mobileIcon" viewBox="0 0 90 157" /*onPointerDown={doShake}*/>
          <rect width="90" height="157" fill="#FFF" rx="2" stroke="#000"/>
          <rect width="81" height="121" x="5" y="14" fill="#000" rx="2"/>
          <circle cx="45" cy="146" r="6.5" stroke="#000"/>
        </svg>
        <div /*onPointerDown={toScene9}*/>Shake!</div>
      </div>
    </div>
    <div className="scene scene9">
      <div className="centerText">
        <img id="logo" src="./media/logo.svg" alt="SEPHORA" />
        <div className="coupon">
          <img src="./media/coupon.jpg" alt="coupon"/>
        </div>
        <div className="disclaimer">
          Offer is valid once per member during Jan 6 - 31, 2021 at in Sephora Hong Kong K11 Art Mall store. Offer is not valid with other discounts and promotion, gift cards, services, charitable items or any other exclusions mentioned in-store, on <a href="https://sephora.hk" target="_blank">sephora.hk</a> and/or and Sephora Hong Kong mobile app.
        </div>
      </div>
    </div>
    <div className="scene sceneError">
      <div className="centerText">
        {errorMsg}
      </div>
    </div>
  </div>
}

export default Main;